import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll3/ViewAll'
import UpdatedAccountDetailsModal from '../../ViewMore/UpdatedAccountDetailsModal'

// Services
import UpdatedAccountInformationService from 'Services/Api/Billing/AccountSetup/UpdatedAccountInformationService'

// Style
import classes from './ClientsWithUpdatedAccountInformationTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}
const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(7, 'days').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(30, 'days').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const ClientsWithUpdatedAccountInformationTile = props => {

  const { service = () => {} } = props

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    )
  }

  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: false,
    orderBy: 'date.created',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})
  const [updateData, setUpdateData] = React.useState({
    show: false
  })

  const showUpdatedInformation = (data) => {
    setUpdateData({
      show: true,
      title: data.billing_client_contract[0]?.client?.account_name,
      subtitle: 'ACCOUNT NO. ' + data.billing_client_contract[0]?.client?.account_number,
      data: data
    })
  }

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    setParams(_params)

    UpdatedAccountInformationService.getUpdatedAccountInformation(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === "boolean" ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    const _params = {
      limit: 6,
      sort: `date.created|desc`
    }
    UpdatedAccountInformationService.getUpdatedAccountInformationTile(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => viewPortGetData()}/>

      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Clients With Updated Account Information'}
        subTitle={'(Contract Details, Member, Related Changes)'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) } }
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{paddingLeft: '6px'}}>Account Name</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
          {tileList && tileList.body && tileList.body.map((item, index) => (
            <TableRow>
                <TableCell style={{paddingLeft: '6px'}}>{item.billing_client_contract ? item.billing_client_contract[0]?.client?.account_name : ''}</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                    <Link onClick={() => showUpdatedInformation(item)}>
                        <Typography variant='caption' className='text-link underline'>View</Typography>
                    </Link>
                </TableCell>
            </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'updated-accounts-modal'}
        config={config}
        tile={'updatedAccountInformation'}
        type='Clients With Updated Account Information'
        title='List of Updated Account Information'
        placeholder='Search List of Updated Account Information'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['billing_client_contract.client.account_number', 'billing_client_contract.client.account_name', 'sending_module', 'category', 'date.created|isDate']}
        displaySettingTitle='Date Received Date Range *'
        startDateError = 'Please enter a valid date received start date'
        endDateError = 'Please enter a valid date received end date'
        laterStartDateError = 'Please select a date received start date that is not later than the current date'
        laterEndDateError = 'Please select a date received end date that is not later than the current date'
        startDateGreaterThanEndDateError = 'Please select a date received start date that is not later than the end date'
      />
      <div>
        <UpdatedAccountDetailsModal updateData={updateData} setUpdateData={setUpdateData} fromTile={true}/>
      </div>
    </React.Fragment>
  )
}

ClientsWithUpdatedAccountInformationTile.displayName = 'DashboardTileComponent'

export default ClientsWithUpdatedAccountInformationTile
