import instance from '../../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/b/s')
const axiosSOA = instance('/b/s')

export default class BillingSOAServices {
  static getClientSOA (params) {
    return axiosSOA({
      url: `/billing/client-soa/${params.soaNumber}/pre-generate-soa`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getMemberPreSOAsToAnalyze (params) {
    let entity = 'Member'
    return axios({
      url: `/billing/${entity}/list/${params.currentUserId}/pre-generate-soa`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getMemberPreSOAsToAnalyzeTile (params) {
    let entity = 'Member'
    return axios({
      url: `/billing/${entity}/list/${params.currentUserId}/pre-generate-soa-tile`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getMemberPreSOAToAnalyze ({ entity = 'member', soaNumber }) {
    return axios({
      url: `/billing/${entity}/${soaNumber}/pre-generate-soa`,
      method: 'GET'
    })
  }

  static getMemberPreSOAToValidate (params) {
    let entity = 'Member'
    return axios({
      url: `/billing/${entity}/list-submitted-for-approval/pre-generate-soa`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getMemberPreSOAToValidateTile (params) {
    let entity = 'Member'
    return axios({
      url: `/billing/${entity}/list-submitted-for-approval/pre-generate-soa-tile`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static routeMemberSOA ({ soaNumber, action }) {
    let entity = 'member'
    return axios({
      url: `/billing/${entity}/route-ticket/pre-generate-soa?action=${action}&soaNumber=${soaNumber}`,
      method: 'PUT'
    })
  }

  static assignMemberSOA ({ soaNumber }) {
    let entity = 'member'
    return axios({
      url: `/billing/${entity}/assign/${soaNumber}/pre-generate-soa`,
      method: 'PUT'
    })
  }

  static getClientFeeColumns ({soaNumber}) {
    return axios({
      url: `/billing/client-soa/${soaNumber}/fee-columns`,
      method: 'GET'
    })
  }

  static getClientFees ({soaNumber}) {
    return axios({
      url: `/billing/client-soa/${soaNumber}/fees`,
      method: 'GET'
    })
  }

  static getApprovedClientSOAs ({ currentUserId }) {
    return axiosSOA({
      url: `/billing/client-soa/list-approve/${currentUserId}/pre-generate-soa`,
      method: 'GET'
    })
  }

  static printSOABatch ({ data }) {
    return axios({
      url: `/billing/client/soa-batch/print`,
      method: 'POST',
      data
    })
  }

  static removeClientLineItems ({ soaNumber, data }) {
    return axios({
      url: `/billing/client-soa/remove-line-items/${soaNumber}`,
      method: 'PATCH',
      data
    })
  }

  static getClientJournalMembers (params) {
    return axios({
      url: `/billing/journal/${params.journalID}/members/`,
      method: 'GET',
      params,
      headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }
}
