import React, { useState, useEffect } from 'react'
import { Button } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { Exposure } from '@material-ui/icons'

import _ from 'lodash'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import BillingCyclesService from 'Services/Api/Billing/BillingCycles/maintenance/BillingCyclesService'

// Hooks
import { useHistory, Link } from 'react-router-dom'
import useForm from 'Hooks/useForm'

// Components
import PageFooter from '../../../Components/PageFooter/PageFooter'
import Select from '../../../Components/FormComponents/Select/Select'
import TimePicker from '../../../Components/FormComponents/TimePicker/TimePicker'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import TreeViewTable from 'StandardComponents/TreeViewTable/TreeViewTable'
import CollapsibleRow from 'StandardComponents/TreeViewTable/CollapsibleRow'
import NormalRow from 'StandardComponents/TreeViewTable/NormalRow'
import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'
import ConfirmDialog from './ConfirmDialog'

// import Editor from './Editor'
// import { DEFAULT_VALUE } from './Editor/constants'
// import PublishDialog from './PublishDialog'

// Utilities
import get from 'lodash/get'

// MuiStyles
import { withStyles } from '@material-ui/core/styles'

// Styles
import classes from './BillRunSetup.module.scss'

const moment = require('moment')

const header_items = [
  {
    id: 'billing_code',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Billing Cycle Code',
    accessor: 'billing_code'
  },
  {
    id: 'billing_type',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Billing Cycle',
    accessor: 'billing_type'
  },
  {
    id: 'description',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Bill Run Time',
    accessor: 'description'
  },
  {
    id: 'transaction_type',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Pre-generate Bill',
    accessor: 'transaction_type'
  },
  {
    id: 'transaction_type',
    show: true,
    center: false,
    disablePadding: true,
    label: 'Due Date',
    accessor: 'transaction_type'
  },
  {
    id: 'action',
    numeric: false,
    center: false,
    disablePadding: true,
    label: ''
  }
]

const StyledButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    width: '100%'
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

export default function BillingCodeSetup () {
  const history = useHistory()
  const table_columns = ['code', 'name', 'bill_run_time', 'pre_generate_bill', 'due_date']
  // const sub_columns = ['', 'billing_code', 'billing_type', 'description', 'transaction_type', 'transaction_type']

  const initialValue = {
    show: false,
    count: 1,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    body: [],
    orderBy: 'billing_code',
    order: true,
    sort: '',
    accessors: ['code', 'name', 'bill_run_time', 'pre_generate_bill', 'due_date']
  }

  const [collapseAll, setCollapseAll] = useState(false)
  const [headers, setHeaders] = useState(header_items)
  const [isFetching, setIsFetchingState] = useState(false)
  const [list, setList] = useState(initialValue)
  const [params, setParams] = useState({})
  const [selectedCodeId, setSelectedCodeId] = useState(null)
  const [edit, setEdit] = useState(false)
  const [deactivate, setDeactivate] = useState(false)
  const [activate, setActivate] = useState(false)
  const [hasConnectedClient, setHasConnectedClient] = useState(false)
  const [connectedClients, setConnectedClients] = useState([])
  const [cycleScheduleOptions, setCycleScheduleOptions] = useState([{ value: null, text: '' }])
  const [cycleDayOptions, setCycleDayOptions] = useState([{ value: null, text: '' }])
  const [tagLine, setTagLine] = useState('')

  const [modalState, setModalState] = useState({
    openSubmitDialog: true,
    title: '',
    confirmationText: '',
    buttonText: '',
    successfulTitle: '',
    successfulText: '',
    submitStatus: false
  })

  const form = useForm({
    cycle_schedule: '',
    cycle_day: [],
    bill_run_time: '08:00 AM',
    pregen_count: 0
  })

  const initializeForm = () => {
    const initialValue = {
      cycle_schedule: cycleScheduleOptions[0].value,
      cycle_day: [],
      bill_run_time: '08:00 AM',
      pregen_count: 0
    }
    form.setValues(initialValue)
  }

  const handleDateChanges = date => {
    console.log(date)
    form.updateValue('bill_run_time', String(date.format('hh:mm A')))
  }

  const handleCycleScheduleChanges = event => {
    form.onChange(event)
    // console.log('FORM: ', form.values)
    fetchCycleDay(form.values.cycle_schedule).then(() => {
      let cycleDay = []
      if (form.values.cycle_schedule === 'SEMI-MONTHLY' || form.values.cycle_schedule === 'BI-WEEKLY') {
        cycleDay =
          form.values.cycle_day !== [] ? form.values.cycle_day : [cycleDayOptions[0].value, cycleDayOptions[0].value]
      } else {
        cycleDay = form.values.cycle_day !== [] ? form.values.cycle_day : [cycleDayOptions[0].value]
      }

      form.updateValue('cycle_day', cycleDay)
      console.log('FORM: ', form.values)

      const tagLine =
        form.values.cycle_schedule === 'QUARTERLY'
          ? ' of the 1st month of the quarter'
          : form.values.cycle_schedule === 'MONTHLY' || form.values.cycle_schedule === 'SEMI-MONTHLY'
          ? ' of the month'
          : ''

      setTagLine(tagLine)
    })
  }

  const handleCycleDayChanges = event => {
    let modifiedEvent = event
    let cycleDay = form.values.cycle_day
    switch (event.target.name) {
      case 'cycle_day1':
        cycleDay[0] = event.target.value
        break
      case 'cycle_day2':
        cycleDay[1] = event.target.value
        break
      default:
        // cycleDay[0] = event.target.value
        break
    }
    modifiedEvent.target.value = cycleDay
    modifiedEvent.target.name = 'cycle_day'
    form.onChange(event)
  }

  const handleDeactivationModal = async selectedCycleEntry => {
    await fetchConnected(selectedCycleEntry.code)
    console.log(hasConnectedClient)
    if (!setHasConnectedClient) {
      setDeactivate(true)
      // setColoredRow(editId)
      setSelectedCodeId(selectedCycleEntry.code)
      setEdit(false)
      initializeForm()
    }
  }

  const handleActivationModal = (billingCode, editId) => {
    setActivate(true)
    // setColoredRow(editId)
    setSelectedCodeId(billingCode._id)
    setEdit(false)
    initializeForm()
  }

  const handleTableCollapseAll = () => {
    setCollapseAll(!collapseAll)
  }

  const storeBillingCycle = () => {
    try {
      const error = {}
      error.cycle_schedule = !form.values.cycle_schedule ? 'Please select a value' : ''
      error.cycle_day = !form.values.cycle_day ? 'Please select a value' : ''
      error.bill_run_time = !form.values.bill_run_time ? 'Please select a value' : ''
      error.pregen_count = !form.values.pregen_count ? 'Please select a value' : ''
      form.setErrors(error)

      if (!error.cycle_schedule && !error.cycle_day && !error.bill_run_time && !error.pregen_count) {
        const codeData = {
          cycle_schedule: form.values.cycle_schedule,
          cycle_day: form.values.cycle_day,
          bill_run_time: form.values.bill_run_time,
          pregen_count: form.values.pregen_count
        }
        if (edit) {
          BillingCyclesService.updateBillingCycle({ codeId: selectedCodeId, data: codeData }).then(() => {
            fetchData()
            setEdit(false)
            initializeForm()
            form.updateValue('cycle_schedule', cycleScheduleOptions[0].value)
          })
        } else {
          BillingCyclesService.storeBillingCycle({ data: codeData }).then(() => {
            fetchData()
            setEdit(false)
            initializeForm()
            form.updateValue('cycle_schedule', cycleScheduleOptions[0].value)
          })
        }
      }
    } catch (error) {
      console.log(`Error storing template image attachment: ${error}`)
    }
  }

  const editBillingCycle = selectedCycleEntry => {
    setEdit(true)
    console.log('###########################################', selectedCycleEntry.code)
    setSelectedCodeId(selectedCycleEntry.code)
    let formValues = {}
    BillingCyclesService.getBillingCycle({ codeId: selectedCycleEntry.code }).then(values => {
      const cycleRecord = values.data.data.pop()
      console.log(cycleRecord)
      let cycleDay = []
      const criterias = cycleRecord.frequency.criterias
      console.log(criterias)

      for (const criteria of criterias) {
        const selectedSchedule = String(criteria.display_name).replace('Every ', '')
        cycleDay.push(selectedSchedule.toUpperCase())
      }

      let schedule = cycleScheduleOptions.find(o => o.text === cycleRecord.frequency.name)

      console.log('===============================', cycleDay)

      formValues = {
        cycle_schedule: schedule.value,
        cycle_day: cycleDay,
        bill_run_time: String(cycleRecord.bill_run_time),
        pregen_count: cycleRecord.pre_generate_formula ? parseInt(cycleRecord.pre_generate_formula.unit) : 0
      }
      form.setErrors(null)
      form.setValues(formValues)
    })
  }

  const deactivateBillingCycle = (selectedCycleEntry, editId) => {
    BillingCyclesService.getAllConnectedAccounts().then(values => {
      const { data } = values.data
      if (data.contracts === []) {
        setHasConnectedClient(true)
      } else {
        setDeactivate(true)
        // setColoredRow(editId)
        setSelectedCodeId(selectedCycleEntry)
        setEdit(false)
        initializeForm()
      }
    })
  }

  const setRecordActivationStatus = (codeId, status) => {
    BillingCyclesService.updateBillingCycle(codeId, { inactive: status }).then(() => {
      console.log('executed!!')
    })
  }

  const activateBillingCycle = (billingCode, editId) => {
    setDeactivate(true)
    // setColoredRow(editId)
    setSelectedCodeId(billingCode._id)
    setEdit(false)
    initializeForm()
  }

  // console.log('SET ON EDIT: ', form.values)

  const multipleActionsDeactivation = [
    { label: 'Edit', handleClick: editBillingCycle },
    { label: 'Deactivate', handleClick: handleDeactivationModal }
  ]
  const multipleActionsActivation = [
    { label: 'Edit', handleClick: editBillingCycle },
    { label: 'Activate', handleClick: handleActivationModal }
  ]

  const fetchConnected = async codeId => {
    BillingCyclesService.getAllConnectedAccounts({ codeId: codeId }).then(values => {
      const { contracts } = values.data.data.pop()
      setConnectedClients(contracts)
      if (contracts > 0) setHasConnectedClient(true)
      else setHasConnectedClient(false)
    })
  }

  const fetchData = params => {
    if (params) {
      setCollapseAll(true)
    }
    setIsFetchingState(true)
    const _params = {
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    setParams(_params)
    BillingCyclesService.getAllBillingCycles().then(({ data: res }) => {
      const { data } = res
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({
        ...i,
        body: data,
        count: data.length,
        sort: _params.sort,
        orderBy: orderByValue,
        order: orderValue
      }))
      setIsFetchingState(false)
    })
  }

  const fetchCycleSchedule = async params => {
    await BillingCyclesService.getAllBillingCyclesScheduleList({ type: 'BILLING_CYCLE_FREQUENCY' }).then(
      ({ data: res }) => {
        const { data } = res
        setCycleScheduleOptions(data)
      }
    )
  }

  const fetchCycleDay = async params => {
    const { type } = params

    let dayType = ' '
    switch (type) {
      case 'DAILY':
        dayType = 'BILLING_CYCLE_VARIABLE_DAILY'
        break
      case 'BI-WEEKLY':
        dayType = 'BILLING_CYCLE_VARIABLE_WEEK'
        break
      case 'WEEKLY':
        dayType = 'BILLING_CYCLE_VARIABLE_WEEK'
        break
      case 'SEMI-MONTHLY':
        dayType = 'BILLING_CYCLE_VARIABLE_DAY'
        break
      case 'MONTHLY':
        dayType = 'BILLING_CYCLE_VARIABLE_DAY'
        break
      case 'QUARTERLY':
        dayType = 'BILLING_CYCLE_VARIABLE_DAY'
        break
      default:
        dayType = ' '
        break
    }
    BillingCyclesService.getAllBillingCyclesScheduleList({ type: dayType }).then(({ data: res }) => {
      const { data } = res
      setCycleDayOptions(data)
    })
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value
    }
    fetchData(params)
  }, 500)

  useEffect(() => {
    fetchCycleSchedule()
  }, [])

  useEffect(() => {
    if (cycleScheduleOptions.length > 0) form.updateValue('cycle_schedule', cycleScheduleOptions[0].value)
  }, [cycleScheduleOptions])

  useEffect(() => {
    const params = { type: form.values.cycle_schedule }
    fetchCycleDay(params)
  }, [form.values.cycle_schedule])

  useEffect(() => {
    initializeForm()
    BreadcrumbsService.setSelectedItems(['collectionMaintenance'])
    fetchData()
  }, [cycleDayOptions])

  // console.log('FORM: ', form.values)

  return (
    <div className={classes.root}>
      <Grid container spacing={1} style={{ marginTop: '1rem', padding: '0 0 10px 10px' }}>
        <Grid item container spacing={2} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Billing Cycle *
            </Typography>
          </Grid>

          <Grid item xs={2}>
            <Select
              name='cycle_schedule'
              placeholder={form.values.cycle_schedule}
              value={form.values.cycle_schedule}
              onChange={handleCycleScheduleChanges}
              data={cycleScheduleOptions}
              variant='outlined'
              error={get(form.errors, 'cycle_schedule')}
            />
          </Grid>

          {form.values.cycle_schedule !== 'DAILY' && (
            <Grid item xs={1}>
              <Typography
                variant='subtitle2'
                style={{ 'text-align': 'center', padding: '0 5px 0 5px ', fontWeight: 'bold' }}
              >
                every
              </Typography>
            </Grid>
          )}

          {form.values.cycle_schedule !== 'DAILY' && (
            <Grid item xs={2}>
              <Select
                // defaultItemText='Select if Parent'
                name='cycle_day1'
                placeholder={form.values.cycle_day[0]}
                value={form.values.cycle_day[0]}
                onChange={handleCycleDayChanges}
                data={cycleDayOptions}
                variant='outlined'
                error={get(form.errors, 'cycle_day')}
              />
            </Grid>
          )}

          {(form.values.cycle_schedule === 'BI-WEEKLY' || form.values.cycle_schedule === 'SEMI-MONTHLY') && (
            <Grid item xs={1}>
              <Typography
                variant='subtitle2'
                style={{ 'text-align': 'center', padding: '0 5px 0 5px ', fontWeight: 'bold' }}
              >
                and
              </Typography>
            </Grid>
          )}

          {(form.values.cycle_schedule === 'BI-WEEKLY' || form.values.cycle_schedule === 'SEMI-MONTHLY') && (
            <Grid item xs={2}>
              <Select
                // defaultItemText='Select if Parent'
                name='cycle_day2'
                placeholder={form.values.cycle_day.length > 1 ? form.values.cycle_day[1] : ''}
                value={form.values.cycle_day.length > 1 ? form.values.cycle_day[1] : ''}
                onChange={handleCycleDayChanges}
                data={cycleDayOptions}
                variant='outlined'
                error={get(form.errors, 'cycle_day')}
              />
            </Grid>
          )}

          {tagLine !== '' && (
            <Grid item xs={1}>
              <Typography
                variant='subtitle2'
                style={{ 'text-align': 'center', padding: '0 5px 0 5px ', fontWeight: 'bold' }}
              >
                {tagLine}
              </Typography>
            </Grid>
          )}
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Bill Run Time *
            </Typography>
          </Grid>

          <Grid item xs={2}>
            <TimePicker
              name='bill_run_time'
              placeholder={form.values.bill_run_time}
              value={form.values.bill_run_time}
              onChange={handleDateChanges}
              variant='outlined'
              format='hh:mm A'
              error={get(form.errors, 'bill_run_time')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Pre-generate statement
            </Typography>
          </Grid>

          <Grid item xs={1}>
            <TextField
              variant='outlined'
              size='small'
              name='pregen_count'
              placeholder='0'
              value={form.values.pregen_count}
              onChange={form.onChange}
              TextFieldStyle={{ marginBottom: '0px', borderColor: '#c7c7c7' }}
              error={get(form.errors, 'pregen_count')}
            />
          </Grid>

          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '0 0px 0 0px', fontWeight: 'bold' }}>
              days before bill run
            </Typography>
          </Grid>

          <Grid item xs={2}>
            <StyledButton onClick={() => storeBillingCycle()}>
              {edit ? 'Update Billing Cycle' : 'Add Billing Cycle'}
            </StyledButton>
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12}>
          <Grid item xs={8}>
            <SearchTextComponent
              placeholder='Search list of Billing Codes'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
          <Grid item xs={2}>
            <Link
              id='display-settings-link'
              className={classes['display-settings']}
              to='/'
              color='primary'
              size='medium'
            >
              <IconButton id='display-settings-icon'>
                <ViewListRoundedIcon className={classes['display-settings-icon']} />
                <Typography className='text-link underline' style={{ color: '#3ab77d' }}>
                  Display Settings
                </Typography>
              </IconButton>
            </Link>
          </Grid>
        </Grid>
      </Grid>
      <div style={{ padding: '0 0 20px 20px' }}>
        <TreeViewTable
          headCells={headers}
          handleCollapseEvent={handleTableCollapseAll}
          isViewTree
          sorter
          filter
          fetchData={fetchData}
          params={params}
          count={list.count}
          size={'md'}
        >
          <TableBody>
            {isFetching && (
              <TableRow>
                <TableCell component='th' colSpan={10}>
                  <CircularLoading />
                </TableCell>
              </TableRow>
            )}
            {!isFetching &&
              list &&
              list.body &&
              list.body.map(
                (item, index) => (
                  <NormalRow
                    index={index}
                    table_columns={table_columns}
                    data={item}
                    id={`link-table-${index}`}
                    style={{ textDecoration: 'none' }}
                    multipleActions={!item.inactive ? multipleActionsDeactivation : multipleActionsActivation}
                  />
                )
                // )
              )}
          </TableBody>
        </TreeViewTable>
      </div>
      <PageFooter isControl handleOnCloseClicked={() => history.push('/')} />
      <ConfirmDialog
        open={deactivate}
        onCancel={() => setDeactivate(false)}
        onSubmit={deactivateBillingCycle}
        title={'Deactivate Billing Run'}
        paragraph1={'Are you sure you want to deactivate the selected Billing Run Entry?'}
        paragraph2={
          'The selected Bill Run entry does not have any assigned client yet, it is safe to deactivate the selected Bill Run.'
        }
      />

      <ConfirmDialog
        open={activate}
        onCancel={() => setActivate(false)}
        onSubmit={activateBillingCycle}
        title={'Deactivate Billing Run'}
        paragraph1={'Are you sure you want to deactivate the selected Billing Run Entry?'}
        paragraph2={
          'The selected Bill Run entry does not have any assigned client yet, it is safe to deactivate the selected Bill Run.'
        }
      />

      <ConfirmDialog
        open={hasConnectedClient}
        // onCancel={() => setHasConnectedClient(false)}
        onSubmit={() => setHasConnectedClient(false)}
        title={'Deactivation Not Allowed'}
        paragraph1={
          'The selected Bill Run is currently assigned to a Client. Deactivating this Bill Run entry will not be Allowed.'
        }
        submitLabel={'OK'}
        cancelBtn={false}
      />

      {/* 
      <ConfirmDialog
        open={successUpdate}
        onCancel={() => setSuccessUpdate(false)}
        onSubmit={() => setSuccessUpdate(false)}
        title={'Billing Code Update Saved Successfully'}
        paragraph1={'The changes/updates made to the selected billing code has been successfully updated.'}
        submitLabel={'OK'}
        cancelBtn={false}
      /> */}
    </div>
  )
}
