import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import Checkbox from '@material-ui/core/Checkbox'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import Typography from '@material-ui/core/Typography'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import Grid from '@material-ui/core/Grid'
import { Popper, TextField, Fade, Button } from '@material-ui/core'
import * as moment from 'moment'

import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons'

// Components
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import EnhancedTable from 'StandardComponents/EnhancedTable/EnhancedTable'
import RemoveSOAEntries from 'StandardComponents/RemoveSOAEntries/RemoveSOAEntries'

import classes from './Analyze.module.scss'
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import BillingSOAServices from 'Services/Api/Billing/SOA/BillingSOAServices'

export default function Analyze (props) {
  const defaultFunction = function () {}
  const { values = {}, handleOnRemoveLineItem = defaultFunction, fetchData, params, refContainer } = props
  const [soaDetails, setSOADetails] = React.useState(null)
  const [lineItems, setLineItems] = React.useState([])
  const [showRemoveButton, setShowRemoveButton] = React.useState(false)
  const [forDeletion, setForDeletion] = React.useState({ account_numbers: [] })
  const [selected, setSelected] = React.useState([])
  const initialHeadCells = [
    { id: 'remove-icon', numeric: false, center: true, disablePadding: false, label: '' },
    { id: 'head-count', numeric: false, center: true, disablePadding: false, label: '#' },
    { id: 'transaction', numeric: false, center: true, disablePadding: false, label: 'TRANSACTION' },
    {
      id: 'member-id',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'MEMBER ID',
      search: true,
      sort: true
      // accessor: '$$member.account_no',
      // sortAccessor: 'journal.member.account_no'
    },
    {
      id: 'last-name',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'LAST NAME',
      search: true,
      sort: true
      // accessor: '$$member.last_name',
      // sortAccessor: 'journal.member.last_name'
    },
    {
      id: 'first-name',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'FIRST NAME',
      search: true,
      sort: true
      // accessor: '$$member.first_name',
      // sortAccessor: 'journal.member.first_name'
    },
    {
      id: 'middle-initial',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'MIDDLE INITIAL',
      search: true,
      sort: true
      // accessor: '$$member.middle_name',
      // sortAccessor: 'journal.member.middle_name'
    },
    {
      id: 'reference-date',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'REFERENCE DATE',
      search: true,
      sort: true
      // accessor: '$$member.effectivity_date|isDate',
      // sortAccessor: 'journal.member.effectivity_date'
    },
    {
      id: 'renewal-date',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'RENEWAL DATE',
      search: true,
      sort: true
      // accessor: '$$member.renewal_date|isDate',
      // sortAccessor: 'journal.member.renewal_date'
    },
    {
      id: 'end-date',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'END DATE',
      search: true,
      sort: true
      // accessor: '$$member.end_date|isDate',
      // sortAccessor: 'journal.member.end_date'
    },
    {
      id: 'plan-type',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'PLAN TYPE',
      search: true,
      sort: true
      // accessor: '$$member.plan_type_name',
      // sortAccessor: 'journal.member.plan_type_name'
    },
    {
      id: 'contract-type',
      numeric: false,
      center: true,
      disablePadding: true,
      label: 'CONTRACT TYPE',
      search: true,
      sort: true
      // accessor: '$$member.plan_type_name',
      // sortAccessor: 'journal.member.plan_type_name'
    },
    // { id: 'membership-fees', numeric: true, disablePadding: false, label: 'MEMBERSHIP FEES' },
    // { id: 'processing-fees', numeric: true, disablePadding: false, label: 'PROCESSING FEES' },
    // TODO these should be dynamic head cells and only applied to member entity
    // { id: 'network-access-fees', numeric: true, disablePadding: false, label: 'NETWORK ACCESS FEES' },
    // { id: 'rsd-fees', numeric: true, disablePadding: false, label: 'RSD FEES' },
    // { id: 'utilization', numeric: true, disablePadding: false, label: 'UTILIZATION' },
    // { id: 'administration-fees', numeric: true, disablePadding: false, label: 'ADMINISTRATION FEES' },
    // { id: 'medicalCollectible', numeric: true, disablePadding: false, label: 'MEDICAL COLLECTIBLE' },
    { id: 'amount', numeric: true, disablePadding: false, label: 'AMOUNT' },
    { id: 'tax-amount', numeric: true, disablePadding: false, label: 'TAX AMOUNT' },
    { id: 'total', numeric: true, disablePadding: false, label: 'TOTAL' },
    { id: 'remarks', numeric: false, center: true, disablePadding: true, label: 'REMARKS' }
  ]
  const [headCells, setHeadCells] = React.useState(initialHeadCells)
  const [clientFeeColumns, setClientFeeColumns] = React.useState([])
  const [clientFees, setClientFees] = React.useState([])
  const [riderColumns, setRiderColumns] = React.useState([])
  const [riderFees, setRiderFees] = React.useState([])
  const [membershipFees, setMembershipFees] = React.useState([])
  const [processingFees, setProcessingFees] = React.useState([])
  // TODO these should be for member entity only
  // const [networkAccessFees, setNetworkAccessFees] = React.useState([])
  // const [rsd, setRsd] = React.useState([])
  // const [utilization, setUtilization] = React.useState([])
  // const [administrationFees, setAdministrationFees] = React.useState([])
  // const [medicalCollectible, setMedicalCollectible] = React.useState([])
  const [showRemoveEntriesDialog, setShowRemoveEntriesDialog] = React.useState(false)
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }
  const [list, setList] = React.useState(initialValue)
  const [listParams, setListParams] = React.useState()
  const [pagination, setPagination] = React.useState()
  React.useEffect(() => {
    console.log(refContainer)
    if (refContainer !== undefined) {
      refContainer.current.parentElement.scrollTo(0, 0)
    }
    console.log('journalId', values.soa.journal)
    if (values.soa) {
      const data = values.soa.client_fees.map(i => ({
        ...i,
        selected: false
      }))
      setSOADetails(values.soa)
      setLineItems({ data: data, indeterminate: undefined, checked: undefined })

      BillingSOAServices.getClientFeeColumns({ soaNumber: values.soa._id.soa_number }).then(value => {
        setClientFeeColumns(value.data.data)
        console.log('[CLIENT FEE COLUMNS] ', value.data.data)
      })

      BillingSOAServices.getClientFees({ soaNumber: values.soa._id.soa_number }).then(value => {
        console.log('[CLIENT FEES] ', value.data.data)
        setClientFees(value.data.data)
      })
    }
  }, [values.soa])
  // const getJournalMembers = (params) => {
  //   const _params = {
  //     limit: list.limit,
  //     page: list.page,
  //     sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
  //     filters: list.filters,
  //     journalID: values.soa.journal._id,
  //     ...params
  //   }
  //   setListParams(_params)
  //   BillingSOAServices.getClientJournalMembers(_params).then(({ data: res }) => {
  //     const { results, count, limit, page } = res.data
  //     console.log('MELVINN', results)
  //     const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
  //     const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
  //     setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
  //   })
  // }

  React.useEffect(() => {
    if (clientFeeColumns) {
      clientFeeColumns.map(cf => {
        let column = {
          id: cf.type.replace(/[^A-Z0-9]/gi, '-').toLowerCase(),
          numeric: false,
          disablePadding: false,
          label: cf.type.toUpperCase()
        }
        let tempHeadCells = initialHeadCells
        tempHeadCells.splice(-4, 0, column)
        setHeadCells(tempHeadCells)
      })
    }
  }, [clientFeeColumns])

  const handleOnClickRemoveOne = ({ objID }) => {
    setForDeletion({ object_ids: [objID] })
    setShowRemoveEntriesDialog(true)
  }

  const handleClick = ({ lineItems, index }) => {
    const _memberLineItems = {
      ...lineItems,
      data: lineItems.data.map((i, _index) => {
        if (_index === index) i.selected = !i.selected

        return i
      })
    }
    setLineItems(_memberLineItems)
    changeSelectAllState({ data: _memberLineItems })
  }

  const handleSelectAllClick = event => {
    let _memberLineItems = lineItems

    if (_memberLineItems.data) {
      _memberLineItems.data.map(i => {
        i.selected = _memberLineItems.indeterminate ? true : !_memberLineItems.checked
        return i
      })
    }

    setLineItems(_memberLineItems)
    changeSelectAllState({ data: _memberLineItems })
    if (_memberLineItems.checked) setShowRemoveButton(false)
    else setShowRemoveButton(true)
  }

  const changeSelectAllState = ({ data }) => {
    let _memberLineItems = data || soaDetails.journal.member
    let selectedCount = 0
    let objIDs = []
    if (_memberLineItems.data) {
      for (let b of _memberLineItems.data) {
        if (b.selected) {
          selectedCount++
          objIDs.push(b._id)
        }
      }
    }

    let indeterminate = selectedCount ? selectedCount < _memberLineItems.data.length : false
    let checked = selectedCount ? selectedCount === _memberLineItems.data.length : false
    _memberLineItems = { ..._memberLineItems, indeterminate, checked }
    setLineItems(_memberLineItems)
    setForDeletion({ object_ids: objIDs })
    if (selectedCount >= 2) setShowRemoveButton(true)
    else setShowRemoveButton(false)
  }

  const handleRemoveLineItem = () => {
    console.log('For Deletion', forDeletion)

    setShowRemoveEntriesDialog(true)
  }

  // For Display Settings
  var today = moment().format('YYYY-MM-DD')
  const [fromDate, setFromDate] = React.useState(today)
  const [toDate, setToDate] = React.useState(today)
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleDisplaySettingsClick = event => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'date-popper' : undefined
  const handleOnApply = event => {
    let fdate = new Date(fromDate)
    let tdate = new Date(toDate)
    const params = {
      range: {
        start: fdate.toISOString(),
        end: tdate.toISOString()
      }
    }
    if (!fdate.valueOf() && !tdate.valueOf()) {
      setFromDateError('Please enter a valid effectivity start date')
      setToDateError('Please enter a valid effectivity end date')
    } else if (fdate.valueOf() > tdate.valueOf()) {
      setToDateError('Please enter a valid effectivity end date that is not later than the current date')
    } else {
      setFromDateError('')
      setToDateError('')
    }
  }
  React.useEffect(() => {
    setPagination({
      count: list ? list.count : 0,
      rowsPerPage: list ? list.limit : 0,
      page: list ? list.page : 0
    })
  }, [list])

  // const changeView = params => {
  //   getJournalMembers(params)
  // }
  return (
    <div className={classes.root}>
      <div className={classes.displaySettings}>
        <Link
          id='display-settings-link'
          className={classes['display-settings']}
          onClick={handleDisplaySettingsClick}
          color='primary'
          size='medium'
        >
          <Grid container id='display-settings-icon'>
            <ViewListRoundedIcon className={classes['display-settings-icon']} />
            <Typography className='text-link underline'>Display Settings</Typography>
          </Grid>
        </Link>
        <Popper id={id} open={open} anchorEl={anchorEl} transition style={{ 'z-index': '1301' }}>
          {({ TransitionProps }) => (
            <Fade {...TransitionProps} timeout={350}>
              <div
                style={{
                  padding: '10px',
                  'background-color': 'white',
                  'box-shadow': '5px 5px rgb(0,0,0,10%)'
                }}
              >
                <Grid container spacing={3}>
                  <Grid item xs={10}>
                    Effectivity Date Range
                  </Grid>
                  <Grid
                    item
                    xs={2}
                    style={{ 'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer' }}
                    onClick={e => handleDisplaySettingsClick(e)}
                  >
                    X
                  </Grid>
                </Grid>
                <Grid container spacing={3}>
                  <Grid item xs={6}>
                    <TextField
                      id='date'
                      type='date'
                      label='From'
                      className={classes.textField}
                      InputLabelProps={{
                        shrink: true
                      }}
                      value={fromDate}
                      onChange={e => {
                        setFromDate(e.target.value)
                      }}
                    />
                  </Grid>
                  <Grid item xs={6}>
                    <TextField
                      id='date'
                      type='date'
                      label='To'
                      className={classes.textField}
                      InputLabelProps={{
                        shrink: true
                      }}
                      value={toDate}
                      onChange={e => {
                        setToDate(e.target.value)
                      }}
                    />
                  </Grid>
                </Grid>
                <Grid container spacing={3} style={{ 'padding-bottom': '50px' }}>
                  <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                    {fromDateError}
                  </Grid>
                  <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                    {toDateError}
                  </Grid>
                </Grid>
                <div>
                  <Button
                    id='submitted-link'
                    variant='contained'
                    onClick={e => handleDisplaySettingsClick(e)}
                    style={{ color: '#3ab77d', 'background-color': 'white', width: '100px', 'margin-left': '140px' }}
                  >
                    Cancel
                  </Button>
                  <Button
                    id='submitted-link'
                    variant='contained'
                    style={{ color: 'white', 'background-color': '#3ab77d', width: '100px', 'margin-left': '20px' }}
                    onClick={handleOnApply}
                  >
                    Apply
                  </Button>
                </div>
              </div>
            </Fade>
          )}
        </Popper>
      </div>
      {values.type !== 'validated' && soaDetails && (
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount Due']}
          summaryValues={
            soaDetails.summary
              ? [
                  soaDetails.summary.sub_total,
                  soaDetails.summary.tax_amount,
                  soaDetails.summary.sub_total + soaDetails.summary.tax_amount
                ]
              : [0, 0, 0]
          }
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-soa-number-value' style={{ fontSize: '20px' }}>
                {soaDetails._id && soaDetails._id.soa_number}
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-soa-staus' color='error'>
                {/* {soaDetails.journal && soaDetails.journal.soa && soaDetails.journal.soa.status} */}
              </Typography>
            </div>
          </div>
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-billing-coverage-field' color='textSecondary'>
                Period Covered
              </Typography>
              <Typography id='content-details-due-date-field' color='textSecondary'>
                Due Date
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-billing-coverage-value' color='textSecondary'>
                {soaDetails.period &&
                  DateFormatterByAbbreviation(soaDetails.period.start) +
                    ' - ' +
                    DateFormatterByAbbreviation(soaDetails.period.end)}
              </Typography>
              <Typography id='content-details-due-date-value' color='textSecondary'>
                {DateFormatterByAbbreviation(soaDetails.due_date)}
              </Typography>
            </div>
          </div>
        </ContentHeader>
      )}
      {showRemoveButton && (
        <div>
          <FontAwesomeIcon icon={faTrashAlt} className={classes['remove-icon']} />
          <Link id='remove-link' onClick={handleRemoveLineItem} className={classes['remove-link']}>
            Remove
          </Link>
        </div>
      )}
      {showRemoveEntriesDialog && (
        <RemoveSOAEntries
          openDialog={showRemoveEntriesDialog}
          setOpenDialog={setShowRemoveEntriesDialog}
          forDeletion={forDeletion}
          handleOnRemoveLineItem={e => {
            handleOnRemoveLineItem(e)
            setLineItems([])
          }}
        />
      )}
      {soaDetails ? (
        <EnhancedTable
          headCells={headCells}
          handleSelectAllClick={handleSelectAllClick}
          indeterminate={!!lineItems.indeterminate}
          checked={!!lineItems.checked}
          selectStatus
          checkAll
          checkboxColor='default'
          // sorter
          fetchData={fetchData}
          params={listParams}
          pagination={pagination}
          count={list.count}
          // changeView={changeView}
          // order={params.order ? params.order : 'asc'}
        >
          <TableBody>
            {lineItems.data &&
              lineItems.data.map((item, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell padding='checkbox'>
                      <Checkbox
                        color='default'
                        // checked={item.selected}
                        // onClick={event => handleClick({ index, lineItems })}
                      />
                    </TableCell>
                    <TableCell>
                      <RemoveCircleIcon
                        id={`remove-${index}`}
                        className={classes.removeIcon}
                        // onClick={event => handleOnClickRemoveOne({ objID: item._id })}
                      />
                    </TableCell>
                    <TableCell id={`head-count-${index}`} align='right'>
                      {index + 1}
                    </TableCell>
                    <TableCell id={`transaction-${index}`}>
                      {soaDetails.transaction_details &&
                        (soaDetails.transaction_details.number ? soaDetails.transaction_details.number : '-') +
                          '\nTransaction Date: ' +
                          (soaDetails.transaction_details.date ? soaDetails.transaction_details.date : '-') +
                          '\nDate Approved: ' +
                          (soaDetails.transaction_details.date_approved
                            ? soaDetails.transaction_details.date_approved
                            : '-')}
                    </TableCell>
                    {item.details && (
                      <React.Fragment>
                        <TableCell id={`account-number-${index}`}>
                          {item.details.account_no ? item.details.account_no : '-'}
                        </TableCell>
                        <TableCell id={`last-name-${index}`}>
                          {item.details.last_name ? item.details.last_name : '-'}
                        </TableCell>
                        <TableCell id={`first-name-${index}`}>
                          {item.details.first_name ? item.details.first_name : '-'}
                        </TableCell>
                        <TableCell id={`middle-initial-${index}`}>
                          {item.details.middle_name ? item.details.middle_name.slice(0, 1) + '.' : '-'}
                        </TableCell>
                        <TableCell id={`reference-date-${index}`}>
                          {item.details.effectivity_date
                            ? moment(item.details.effectivity_date).format('MMM.DD,YYYY')
                            : '-'}
                        </TableCell>
                        <TableCell id={`renewal-date-${index}`}>
                          {/* {soaDetails.journal.client.type === 'Renewed'
                                ? moment(item.renewal_date).format('MMM.DD,YYYY')
                                : ''} */}
                        </TableCell>
                        <TableCell id={`end-date-${index}`}>
                          {item.details.end_date
                            ? moment(item.details.end_date).format('MMM.DD,YYYY')
                            : soaDetails.contract_expiration_date
                            ? moment(soaDetails.contract_expiration_date).format('MMM.DD,YYYY')
                            : '-'}
                        </TableCell>
                        <TableCell id={`plan-type-${index}`}>
                          {item.details.plan_type_name ? item.details.plan_type_name : '-'}
                        </TableCell>
                        <TableCell id={`contract-type-${index}`}>
                          {item.details.contract_type ? item.details.contract_type : '-'}
                        </TableCell>
                      </React.Fragment>
                    )}
                    {clientFeeColumns &&
                      clientFeeColumns.map((column, i) => {
                        if (clientFees && clientFees.findIndex(cf => cf.id === item._id) > -1) {
                          let index = clientFees.findIndex(cf => cf.id === item._id)
                          let lineItem = clientFees[index]
                          if (lineItem.fees.findIndex(fee => fee.code === column.code) > -1) {
                            let feeIndex = lineItem.fees.findIndex(fee => fee.code === column.code)
                            return <TableCell>{CurrencyFormatter(lineItem.fees[feeIndex].value)}</TableCell>
                          } else return <TableCell>{CurrencyFormatter(0)}</TableCell>
                        } else return <TableCell>{CurrencyFormatter(0)}</TableCell>
                      })}
                    {clientFees && clientFees.findIndex(cf => cf.id === item._id) > -1 ? (
                      <React.Fragment>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(clientFees[clientFees.findIndex(cf => cf.id === item._id)].total_fees.total_amt)}
                        </TableCell>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(clientFees[clientFees.findIndex(cf => cf.id === item._id)].total_fees.total_tax_amt)}
                        </TableCell>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(clientFees[clientFees.findIndex(cf => cf.id === item._id)].total_fees.total_fees)}
                        </TableCell>
                      </React.Fragment>
                    ) : (
                      <React.Fragment>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(0)}
                        </TableCell>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(0)}
                        </TableCell>
                        <TableCell id={`total-${index}`} align='right'>
                          {CurrencyFormatter(0)}
                        </TableCell>
                      </React.Fragment>
                    )}
                    <TableCell id={`remarks-${index}`}>{item.details.name && item.details.name}</TableCell>
                  </TableRow>
                )
              })}
          </TableBody>
        </EnhancedTable>
      ) : (
        'No Data'
      )}
    </div>
  )
}
