import React from 'react'
import { Grid, Typography, Tabs, Tab } from '@material-ui/core'

import BillRunSetupPage from './BillRunSetup/BillRunSetup'
import BillingCodeSetupPage from './BillingCodeSetup/BillingCodeSetup'
import BillingTaxChargingSetup from './BillingTaxChargingSetup/BillingTaxChargingSetup'
// import ScheduleLettersPage from './ScheduleLetters/ScheduleLetters'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import classes from './BillingMaintenancePage.module.scss'

export default function BillingMaintenancePage () {
  const pageTitle = ['Bill Run Setup', 'Billing Code Setup', 'Pro-Rata Setup', 'Tax Charging Setup']
  const [selectedTab, setSelectedTab] = React.useState(0)

  const handleChange = (event, newValue) => {
    setSelectedTab(newValue)
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome(['billingMaintenance'])
  })

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>{pageTitle[selectedTab]}</Typography>
            </Grid>
          </Grid>
        </Grid>

        <Tabs indicatorColor='xyz' value={selectedTab} onChange={handleChange}>
          <Tab label='Bill Run Setup' className={selectedTab === 0 ? `${classes['tab-selected']}` : ''} />
          <Tab label='Billing Code Setup' className={selectedTab === 1 ? `${classes['tab-selected']}` : ''} />
          <Tab label='Pro-Rata Setup' className={selectedTab === 2 ? `${classes['tab-selected']}` : ''} />
          <Tab label='Tax Charging Setup' className={selectedTab === 3 ? `${classes['tab-selected']}` : ''} />
        </Tabs>

        {selectedTab === 0 && <BillRunSetupPage />}
        {selectedTab === 1 && <BillingCodeSetupPage />}
        {selectedTab === 3 && <BillingTaxChargingSetup />}
        {/* {selectedTab === 1 && <LetterTemplatePage />}
        {selectedTab === 2 && <ScheduleLettersPage />} */}
      </Grid>
    </div>
  )
}
