/** React Libraries */
import React from 'react'
import { Link } from 'react-router-dom'
/** Material UI Libraries */
import { Typography,
  Box,
  Popper,
  Fade,
  Radio,
  RadioGroup,
  FormControlLabel } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import VisibilityIcon from '@material-ui/icons/Visibility'
import ReplyIcon from '@material-ui/icons/Reply'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit } from '@fortawesome/free-solid-svg-icons'

/** RBAC */
/** Utilities */
import * as moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
/** Standard Components */
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
/** Shared Components */
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import SALES_CHANNEL from 'Pages/Protected/BillingAndCollection/Constant/SalesChannelTypes'
/** Page Components */
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'
/** CSS */
import classes from './DebitMemoApprovedPage.module.scss'
/** Services */
import BillingDebitMemoServices from 'Services/Api/Billing/Transactions/DebitMemo/BillingDebitMemoServices'
import ExportsServices from 'Services/Api/ExportsServices'

const tableHeaders = [
  {
    id: 'approved-debit-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Billing Code & Type *',
    center: false
  },
  {
    id: 'approved-debit-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Member Account ID'
  },
  {
    id: 'approved-debit-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Member Name'
  },
  {
    id: 'approved-debit-description',
    numeric: false,
    disablePadding: false,
    label: 'Description *'
  },
  {
    id: 'approved-debit-tax-type',
    numeric: false,
    disablePadding: false,
    label: 'Taxable Amount *'
  },
  {
    id: 'approved-debit-amount',
    numeric: false,
    disablePadding: false,
    label: 'Tax Amount'
  }
]

export default function DebitMemoPage (props) {
  PageTitleService()
  const { transaction_number } = props.match.params
  const [totalTaxAmount, setTotalTaxAmount] = React.useState(0)
  const [approvedDebitMemo, setApprovedDebitMemo] = React.useState({
    data: {}
  })

  const [debitMemoDetail, setDebitMemoDetail] = React.useState({})

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['dashboard', 'approvedDebitMemoList', `approvedDebitMemo`])
    Promise.all([BillingDebitMemoServices.getTobeRevisedDebitMemo({ transaction_number })]).then(value => {
      const [debit] = value
      setApprovedDebitMemo(prevState => ({
        ...prevState,
        data: debit.data.data
      }))
    })

    // TODO API CALL FOR CURRENT DEBIT MEMO TRANSACTIONS
    fetchData({})
  }, [])

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [params, setParams] = React.useState({})

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      transactionNumber: transaction_number,
      ...params
    }
    setParams(_params)

    BillingDebitMemoServices.getDebitMemoDetails(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({ ...i, body: results, count, limit, page, sort: _params.sort, orderBy: orderByValue, order: orderValue }))
    })
  }

  const transactionInfo = approvedDebitMemo.data
  const clientInfo = approvedDebitMemo.data.client
  const billingInfo = approvedDebitMemo.data.billing
  const contractInfo = approvedDebitMemo.data.contract
  const summaryInfo = approvedDebitMemo.data.summary
  const dateInfo = approvedDebitMemo.data.date
  const reference = approvedDebitMemo.data.reference && approvedDebitMemo.data.reference.reference
  const debitInfo = debitMemoDetail.data

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const changeView = params => {
    fetchData(params)
  }
  // For Modify Bill Run inclusion Popper
  const [billInclusion, setBillInclusion] = React.useState(true)
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
      setTempValue(value)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'transitions-popper' : undefined
  const [value, setValue] = React.useState('Include in the upcoming bill run')
  const [tempValue, setTempValue] = React.useState('Include in the upcoming bill run')
  const [errors, setErrors] = React.useState({})
  const handleChange = (event) => {
    setValue(event.target.value)
  }

  const handleExportDebitMemo = async args => {
    const { list } = args
    const tile = 'Approved Debit Memo Page'
    const toExportList = list.body
    const data = []
    for (const { billingCodeType, memberAccountId, memberName, description, taxType, taxableAmount } of toExportList) {
      await data.push({
        field_1: billingCodeType,
        field_2: memberAccountId,
        field_3: memberName,
        field_4: description,
        field_5: taxType,
        field_6: taxableAmount
      })
    }
    console.log(data)
    ExportsServices.getDebitMemoExports(data, {tile: tile}).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', `${tile}.xlsx`)
      document.body.appendChild(link)
      link.click()
    })
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5'>
          {clientInfo && clientInfo.account_name} - DebitMemo
        </Typography>
        <Typography id='header-details-account-number' color='textSecondary'>
          ACCOUNT NUMBER {clientInfo && clientInfo.account_number}
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'580px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-account-number-value'>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-account-status-value'>{clientInfo && clientInfo.account_status}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-tax-type-value'>{clientInfo && clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-corporate-tin-value'>{clientInfo && clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-billing-cyle-value'>
                {billingInfo && billingInfo.cycle.code + ' ' + billingInfo.cycle.name}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT TYPE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-contract-plan-type-value'>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-effectivity-date-value'>
                {moment(contractInfo && contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-expiration-date-value'>
                {moment(contractInfo && contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-sales-channel-field' color='textSecondary'>
            SALES CHANNEL
              </Typography>
              {clientInfo && clientInfo.sales_channel ? (
                <Typography id='drawer-sales-channel-value'>
                  <Box fontWeight='fontWeightBold'>
                    {clientInfo.sales_channel.type ? clientInfo.sales_channel.type : '-'}
                  </Box>
                </Typography>
              ) : (
                <Typography id='drawer-sales-channel-value'>
                  <Box fontWeight='fontWeightBold'>-</Box>
                </Typography>
              )}
            </div>

            <div className={classes['drawer-content']}>
              {clientInfo && clientInfo.sales_channel && (
                <React.Fragment>
                  <Typography id='drawer-sales-channel-name-field' color='textSecondary'>
                    {clientInfo.sales_channel.type === SALES_CHANNEL.AGENT
                      ? 'AGENT NAME'
                      : clientInfo.sales_channel.type === SALES_CHANNEL.BROKER
                        ? 'NAME OF COMPANY'
                        : clientInfo.sales_channel.type === SALES_CHANNEL.BDO && 'BDO NAME'}
                  </Typography>
                  <Typography id='drawer-sales-channel-name-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.name ? clientInfo.sales_channel.name : '-'}
                    </Box>
                  </Typography>
                </React.Fragment>
              )}
            </div>

            {clientInfo && clientInfo.sales_channel && clientInfo.sales_channel.type === SALES_CHANNEL.BROKER && (
              <React.Fragment>
                <div className={classes['drawer-content']}>
                  <Typography id='drawer-broker-name-field' color='textSecondary'>
                BROKER NAME
                  </Typography>
                  <Typography id='drawer-broker-name-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.contact_person ? clientInfo.sales_channel.contact_person : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-position-field' color='textSecondary'>
                POSITION
                  </Typography>
                  <Typography id='drawer-position-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.position ? clientInfo.sales_channel.position : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-contact-no-field' color='textSecondary'>
                CONTACT NO.
                  </Typography>
                  <Typography id='drawer-contact-no-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.contact_no ? clientInfo.sales_channel.contact_no : '-'}
                    </Box>
                  </Typography>
                </div>

                <div className={classes['drawer-content']}>
                  <Typography id='drawer-email-field' color='textSecondary'>
                EMAIL
                  </Typography>
                  <Typography id='drawer-email-value'>
                    <Box fontWeight='fontWeightBold'>
                      {clientInfo.sales_channel.email_address ? clientInfo.sales_channel.email_address : '-'}
                    </Box>
                  </Typography>
                </div>
              </React.Fragment>
            )}
            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography className={classes['drawer-account-value-field']} id='drawer-location-value'>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>
            <ReplyIcon id='export-icon' className={classes['export-icon']} />
            <Link id='export-link' onClick={() => handleExportDebitMemo({ list: list })} className={classes['export-link']}>
              Export
            </Link>
            <Link id='view-billing-link' aria-describedby={id} onClick={handleClick} className={classes['view-link']}>
              <FontAwesomeIcon id='view-billing-icon' className={classes['view-icon']} icon={faEdit} />
              Modify Bill Run Inclusion
            </Link>
            <Popper style={{'z-index': '3000'}} id={id} open={open} anchorEl={anchorEl} transition>
              {({ TransitionProps }) => (
                <Fade {...TransitionProps} timeout={350}>
                  <div
                    style={{
                      padding: '10px',
                      'background-color': 'white',
                      'box-shadow': '5px 5px rgb(0,0,0,10%)'

                    }}
                  >
                    <div>
                      <span>Select Bill Run inlcusion for this transaction<span style={{'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer'}}
                        onClick={e => {
                          handleClick(e)
                          setValue(tempValue)
                        }}>X</span></span>
                    </div>
                    <div>
                      <RadioGroup aria-label='gender' name='gender1' value={value} onChange={handleChange}>
                        <FormControlLabel value='Include in the upcoming bill run' control={<Radio color='primary' />} label='Include in the upcoming bill run' />
                        <FormControlLabel value='Postpone to the next bill run' control={<Radio color='primary' />} label='Postpone to the next bill run' />
                      </RadioGroup>
                    </div>
                    <div>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        onClick={e => {
                          // handleClick(e) //close modify bill run inclusion pop up
                          setValue(tempValue)
                        }}
                        style={{color: '#3ab77d',
                          'background-color': 'white',
                          width: '100px',
                          'margin-left': '100px',
                          textTransform: 'none'
                        }}
                      >
                        Cancel
                      </Button>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        style={{color: 'white',
                          'background-color': '#3ab77d',
                          width: '100px',
                          'margin-left': '10px',
                          textTransform: 'none'
                        }}
                        onClick={() => {
                          if (value === 'Include in the upcoming bill run') {
                            setBillInclusion(true)
                            setTempValue(value)
                            setValue('Include in the upcoming bill run')
                            setBillInclusion(true)
                          } else {
                            setBillInclusion(false)
                            setValue('Postpone to the next bill run')
                            setTempValue(value)
                            setBillInclusion(false)
                          }
                        }}
                      >
                        Apply
                      </Button>
                    </div>
                  </div>
                </Fade>
              )}
            </Popper>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-debit-number-field' variant='h6' color='textSecondary'>
                DEBIT MEMO
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                PERIOD COVERED
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
              {transactionInfo.soa_number && (
                <Typography id='content-details-soa-number-field' color='textSecondary'>
                  SOA NO
                </Typography>
              )}
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                REFERENCE NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-debit-number-value' variant='h6'>
                {transaction_number}
              </Typography>
              <Typography className={classes['debit-memo-values']} id='content-details-trasaction-date-value'>
                {moment(transactionInfo.transaction && transactionInfo.transaction.date).format('MMM DD, YYYY')}
              </Typography>
              <Typography
                className={classes['debit-memo-values']}
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              >
                {transactionInfo.transaction && transactionInfo.transaction.period.start ? moment(transactionInfo.transaction.period.start).format('MMM DD, YYYY') : ' '}{' '}
                - {transactionInfo.transaction && transactionInfo.transaction.period.end ? moment(transactionInfo.transaction.period.end).format('MMM DD, YYYY') : ' '}
              </Typography>
              <Typography className={classes['debit-memo-values']} id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
              {transactionInfo.soa_number ? (
                <Typography className={classes['debit-memo-values']} id='content-details-soa-number-value' style={{ width: '100px' }}>
                  {transactionInfo.soa_number}
                </Typography>
              ) : (
                <Typography id='content-details-reference-number' style={{ fontWeight: 'bold' }}>
                  {reference && reference.name}
                </Typography>
              )}
            </div>
            <div className={classes.status}>
              <Typography id='content-details-status' style={{ fontSize: '14px', marginTop: '8px', fontWeight: '500' }}>
                APPROVED
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable
          count={list.count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {list.body &&
              list.body.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    key={detailIndex}
                    align='left'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    key={detailIndex}
                    align='left'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    key={detailIndex}
                    align='left'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    key={detailIndex}
                    align='left'
                  >
                    {CurrencyFormatter(detail.taxAmount)}
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='close-link' to={`/`} style={{ textDecoration: 'none' }}>
              <Button id='close-button' variant='contained' className={classes['close-button']}>
                Close
              </Button>
            </Link>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
