import instance from '../../../instance'

const axios = instance('/b/m/')

export default class BillingCyclesService {
  static storeBillingCycle ({ data }) {
    return axios({
      url: `/billing/maintenance/cycles/`,
      method: 'POST',
      data
    })
  }

  static updateBillingCycle ({ codeId, data }) {
    return axios({
      url: `/billing/maintenance/cycles/${codeId}`,
      method: 'PUT',
      data
    })
  }

  static deactivateBillingCycle ({ codeId, data }) {
    return axios({
      url: `/billing/maintenance/cycles/${codeId}`,
      method: 'PATCH',
      data
    })
  }

  static getBillingCycle ({ codeId, params }) {
    return axios({
      url: `/billing/maintenance/cycles/${codeId}`,
      method: 'GET',
      params
    })
  }

  static getAllBillingCycles (params) {
    return axios({
      url: `/billing/maintenance/cycles`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getAllBillingCyclesScheduleList (params) {
    return axios({
      url: `/billing/maintenance/lists`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getAllConnectedAccounts ({ codeId, params }) {
    return axios({
      url: `/billing/maintenance/cycles/accounts/${codeId}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
