/* Utilities */
import _ from 'lodash'

/*  Hooks */
import React from 'react'

/* Material UI Components */
import Paper from '@material-ui/core/Paper'
import Table from '@material-ui/core/Table'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'

/* Custom Components */
import CustomSearchTextComponent from '../../../Components/CustomSearchTextComponent/CustomSearchTextComponent'

/* Icons */
import { faSort } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

/* Styles */
import classes from './CustomEnhancedTable.module.scss'

export default function CustomEnhancedTable (props) {
  const { reset, cancelReset, tableHeaders, tableHeight, children, size, fetchData, params, textBoxHeight, hasLink, hasCheckbox, noSearchIcon = false, checkBoxColor = 'default' } = props

  const onChangeSearch = _.debounce((e) => {
    if (cancelReset !== undefined) {
      cancelReset()
    }
    const value = {
      [e.target.name]: e.target.value
    }
    fetchData({
      filters: { ...params.filters, ...value },
      page: 1
    })
  }, 800)

  const onSort = (name) => {
    const order = !params.order
    fetchData({
      ...params,
      sort: `${name}|${order ? 'asc' : 'desc'}`,
      order: order,
      orderBy: name,
      page: 1
    })
  }

  return (
    <div className={classes.root}>
      <Paper style={{ height: tableHeight }} className={classes.paper}>
        <div className={classes['table-wrapper']}>
          <Table
            stickyHeader
            aria-label='sticky table'
            className={classes.table}
            aria-labelledby='tableTitle'
            size={size}
          >
            <TableHead className={classes['table-head']}>
              <TableRow>
                {hasCheckbox && (
                  <TableCell
                    id='cet-checkbox-column'
                    key='cet-checkbox-column'
                  >
                    <Checkbox color={checkBoxColor} disableRipple className={classes['column-header-checkbox']} />
                  </TableCell>)}
                {tableHeaders.map(header => (
                  <TableCell className={classes['th']}
                    id={header.id}
                    key={header.id}
                    align={header.center ? 'center' : header.numeric ? 'right' : 'left'}
                    padding={header.disablePadding ? 'none' : 'default'}
                    sortDirection={false}
                    width={header.width}
                  >
                    {header.label}
                    {header.label !== '' && header.sort ? (
                      <FontAwesomeIcon className={classes['sort-icon']} onClick={() => onSort(header.accessor)} icon={faSort} />
                    ) : (
                      ''
                    )}
                    {header.label !== '' && header.search ? <CustomSearchTextComponent reset={reset} name={header.accessor} width={header.width} height={textBoxHeight} onChange={onChangeSearch} onSort={onSort} noIcon={noSearchIcon} /> : ''}
                  </TableCell>
                ))}
                {hasLink && (<TableCell
                  id='cet-link-column'
                  key='cet-link-column'
                />)}
              </TableRow>
            </TableHead>
            {children}
          </Table>
        </div>
      </Paper>
    </div>
  )
}
