import instance from '../../../instance'

const axios = instance('/b/m/')

export default class BillingCyclesService {
  static getMany (params) {
    return axios({
      url: `/billing/maintenance/tax`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static update ({code, data}) {
    return axios({
      url: `/billing/maintenance/tax/${code}`,
      method: 'PUT',
      data: data
    })
  }

  static patch ({code, data}) {
    return axios({
      url: `/billing/maintenance/tax/${code}`,
      method: 'PATCH',
      data: data
    })
  }

  static store (data) {
    return axios({
      url: `/billing/maintenance/tax`,
      method: 'POST',
      data
    })
  }
}
