/* Utilities */
import _ from 'lodash'
import get from 'lodash/get'

/*  Hooks */
import useForm from 'Hooks/useForm'
import React, { useState, useEffect } from 'react'
import { useHistory, Link } from 'react-router-dom'

/* Material UI Components */
import Grid from '@material-ui/core/Grid'
import { Button } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

/* Custom Components */
import BillingTaxChargingCustomTable from './BillingTaxChargingCustomTable'

import PageFooter from '../../../Components/PageFooter/PageFooter'
import TextField from '../../../Components/FormComponents/TextField/CustomTextField'
import CustomDialog from '../../../Components/BillingMaintenanceDialog/CustomizedDialogs'

import CircularLoading from 'SharedComponents/CircularLoading/CircularLoading'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

/* MUI Styles */
import { withStyles } from '@material-ui/core/styles'

/* Styles */
import classes from './BillingTaxChargingSetup.module.scss'

/*  Services */
import BillingTaxChargingService from 'Services/Api/Billing/BillingTaxCharging/maintenance/BillingTaxChargingService'

const tableHeadersItems = [
  {
    id: 'tax-code-id',
    numeric: false,
    disablePadding: false,
    label: 'Tax Code*',
    center: false,
    search: true,
    sort: true,
    accessor: 'code'
  },
  {
    id: 'tax-type-id',
    numeric: false,
    disablePadding: false,
    label: 'Tax Type*',
    center: false,
    search: true,
    sort: true,
    accessor: 'tax_type'
  },
  {
    id: 'description-id',
    numeric: false,
    disablePadding: false,
    label: 'Description',
    search: true,
    sort: true,
    accessor: 'description'
  },
  {
    id: 'percentage-id',
    numeric: false,
    disablePadding: false,
    label: 'Percentage',
    search: true,
    sort: true,
    accessor: 'percentage'
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    label: '',
    search: true,
    sort: true,
    accessor: ''
  }
]

const BillingTaxConfigCustomButton = withStyles({
  root: {
    background: '#3ab77d',
    '&:hover': {
      background: '#0b4930'
    },
    borderRadius: 3,
    boxShadow: 'none',
    border: 0,
    color: 'white',
    height: 45,
    width: '100%'
  },
  label: {
    textTransform: 'capitalize'
  }
})(Button)

export default function BillingTaxChargingSetup () {
  const history = useHistory()

  const initialValue = {
    show: false,
    count: 1,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    body: [],
    orderBy: 'code',
    order: true,
    sort: '',
    accessors: [
      'code',
      // 'tax_type',
      // 'percentage',
      'description'
    ]
  }

  const form = useForm({
    tax_code: '',
    tax_type: '',
    description: '',
    percentage: ''
  })

  const [list, setList] = useState(initialValue)
  const [listItem, setListItem] = useState(null)

  const [edit, setEdit] = useState(false)
  const [page, setPage] = useState(null)
  const [params, setParams] = useState({})
  const [openDialog, setOpenDialog] = useState(false)

  const [isFetching, setIsFetchingState] = useState(false)

  useEffect(() => {
    fetchData()
  }, [])

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      search: list.search,
      accessors: list.accessors,
      ...params
    }
    setParams(_params)
    BillingTaxChargingService.getMany(_params).then(({ data: res }) => {
      const { results, count } = res.data
      const orderByValue = _params.orderBy ? _params.orderBy : list.orderBy
      const orderValue = typeof _params.order === 'boolean' ? _params.order : list.order
      setList(i => ({
        ...i,
        body: results,
        count: count,
        sort: _params.sort,
        orderBy: orderByValue,
        order: orderValue
      }))
      setIsFetchingState(false)
    })
  }

  const mainSearchChange = _.debounce(e => {
    const params = {
      search: e.target.value
    }
    fetchData(params)
  }, 500)

  const handleEdit = detail => {
    const formValues = {
      tax_code: detail.code,
      tax_type: detail.tax_type,
      description: detail.description,
      percentage: detail.percentage * 100
    }

    setEdit(true)
    form.setErrors(null)
    form.setValues(formValues)
  }

  const handleDeactivate = detail => {
    setListItem(detail)
    setOpenDialog(true)
  }

  const handleAddTaxCharging = async () => {
    try {
      const error = {}
      error.tax_type = !form.values.tax_type ? 'Please enter Tax Type' : ''
      error.description = !form.values.description ? 'Please ente the Tax Type description' : ''
      error.percentage = !form.values.percentage ? 'Please enter the Tax Type Percentage' : ''
      form.setErrors(error)

      if (!error.tax_type && !error.description && !error.percentage) {
        if (edit) {
          setPage('update')
          setOpenDialog(true)
        } else {
          await BillingTaxChargingService.store({
            taxType: form.values.tax_type,
            description: form.values.description,
            percentage: form.values.percentage
          })
          fetchData()
          form.reset()
        }
      }
    } catch (error) {
      console.log(`Error storing template image attachment: ${error}`)
    }
  }

  const handleSubmit = async () => {
    if (edit) {
      await BillingTaxChargingService.update({
        code: form.values.tax_code,
        data: {
          taxType: form.values.tax_type,
          description: form.values.description,
          percentage: form.values.percentage
        }
      })
    } else {
      await BillingTaxChargingService.patch({
        code: listItem.code,
        data: {inactive: !listItem.inactive}
      })
    }

    fetchData()
    setEdit(false)
    form.reset()
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} style={{ marginTop: '1rem', padding: '0 0 10px 10px' }}>
        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Tax Type *
            </Typography>
          </Grid>

          <Grid item xs={3}>
            <TextField
              variant='outlined'
              size='small'
              name='tax_type'
              placeholder='Enter Tax Type'
              value={form.values.tax_type}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'tax_type')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Description *
            </Typography>
          </Grid>

          <Grid item xs={9}>
            <TextField
              variant='outlined'
              size='small'
              name='description'
              placeholder='Enter Tax Type Description'
              value={form.values.description}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'description')}
            />
          </Grid>
        </Grid>

        <Grid item container spacing={4} xs={12}>
          <Grid item xs={2}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              Percentage *
            </Typography>
          </Grid>

          <Grid item xs={3}>
            <TextField
              variant='outlined'
              size='small'
              name='percentage'
              placeholder='Enter Percentage'
              value={form.values.percentage}
              onChange={form.onChange}
              TextFieldStyle={{marginBottom: '0px', borderColor: '#c7c7c7'}}
              error={get(form.errors, 'percentage')}
            />
          </Grid>
          <Grid item xs={0}>
            <Typography variant='subtitle2' style={{ padding: '15px 0 0 15px', fontWeight: 'bold' }}>
              %
            </Typography>
          </Grid>

          <Grid item xs={2}>
            <BillingTaxConfigCustomButton style={{ marginTop: '5px' }} onClick={() => handleAddTaxCharging()} >{edit ? 'Update Tax Charging' : 'Add Tax Charging'}</BillingTaxConfigCustomButton>
          </Grid>
        </Grid>

        <Grid item container spacing={2} xs={12} style={{margin: '10px 10px 0 0'}}>
          <Grid item xs={9}>
            <SearchTextComponent
              placeholder='Search from the list of Tax Charging'
              onChange={e => {
                e.persist()
                mainSearchChange(e)
              }}
            />
          </Grid>
          <Grid item xs={3}>
            <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
              <IconButton id='display-settings-icon'>
                <ViewListRoundedIcon className={classes['display-settings-icon']} />
                <Typography className='text-link underline' style={{color: '#3ab77d'}}>Display Settings</Typography>
              </IconButton>
            </Link>
          </Grid>
        </Grid>
      </Grid>

      <div style={{ padding: '0 0 20px 20px' }}>
        <BillingTaxChargingCustomTable
          sort
          search
          params={params}
          count={list.count}
          fetchData={fetchData}
          tableHeaders={tableHeadersItems}>

          <TableBody>
            {isFetching && (
              <TableRow>
                <TableCell component='th' colSpan={10}>
                  <CircularLoading />
                </TableCell>
              </TableRow>
            )}

            {!isFetching && list && list.body &&
                list.body.map((detail, detailIndex) => (
                  <TableRow style={{ backgroundColor: 'red' }} hover tabIndex={-1} key={detailIndex}>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.code}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.tax_type}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {detail.description}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      {(detail.percentage * 100) + '%'}
                    </TableCell>
                    <TableCell
                      key={detailIndex}
                      align='left'
                    >
                      <Link onClick={() => handleEdit(detail)} className={classes.link} style={{ textDecoration: 'underline', marginRight: '10px' }}>
                        Edit
                      </Link>
                      <Link onClick={() => handleDeactivate(detail)} className={classes.link} style={{ textDecoration: 'underline', marginRight: '10px' }}>
                        {detail.inactive ? 'Activate' : 'Deactivate'}
                      </Link>
                    </TableCell>
                  </TableRow>
                ))}
          </TableBody>
        </BillingTaxChargingCustomTable>
      </div>

      <PageFooter isControl handleOnCloseClicked={() => history.push('/')} />

      {openDialog === true ? (
        <CustomDialog
          page={page}
          openDialog={openDialog}
          setOpenDialog={setOpenDialog}
          submitFunction={handleSubmit}
        />
      ) : ''}
    </div>
  )
}
