import 'date-fns'
import React, { useState, useEffect, SyntheticEvent } from 'react'
import PropTypes from 'prop-types'
import MomentUtils from '@date-io/moment'
import Typography from '@material-ui/core/Typography'
import { MuiPickersUtilsProvider, KeyboardTimePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'

import FormControl from '@material-ui/core/FormControl'
import FormHelperText from '@material-ui/core/FormHelperText'

import classes from './TimePicker.module.scss'
import { isInteger } from 'lodash'

const moment = require('moment')

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardTimePicker
 */

export default function TimePicker (props) {
  const {
    id,
    label,
    required,
    format,
    disableComponents = false,
    readOnly,
    onChange,
    value,
    orientation = '',
    errorMessage = null,
    placeholder = null,
    error = null,
    ...other
  } = props

  const theme = createMuiTheme({
    overrides: {
      MuiInput: {
        underline: {
          '&&&&:hover:before': {
            borderBottom: 'none'
          },
          '&&&&:before': {
            borderBottom: 'none'
          },
          '&&&&:after': {
            borderBottom: 'solid 1px rgba(0,0,0,.2)'
          },
          '&&&&:focus:before': {
            borderBottom: 'none'
          },
          '&&&&:focus:after': {
            borderBottom: 'none'
          }
        }
      }
    }
  })

  const convertTimestringtoDate = time => {
    const isPM = String(time).match('PM')
    const timeString = String(time)
      .replace(' AM', '')
      .replace(' PM', '')
      .split(':')

    let result = new Date()

    try {
      result.setMilliseconds(0)
      result.setMinutes(parseInt(timeString[1]))
      result.setHours(isPM ? parseInt(timeString[0]) + 12 : parseInt(timeString[0]))
    } catch (error) {
      result = errorMessage
    }

    return result
  }

  const [timeValue, setTimeValue] = useState(null)

  const handleTimeChange = date => {
    onChange(date)
  }

  const handleError = error => {
    console.log(error)
  }

  const initializeValues = async () => {
    await setTimeValue(moment(convertTimestringtoDate(value)))
  }

  useEffect(() => {
    initializeValues()
  }, [value])

  return (
    // <FormControl error={error.length > 0} className={classes.formControl}>
    <div className={`${classes.root} ${classes[orientation]}`}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {String(timeValue)}
          </Typography>
        ) : (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <MuiThemeProvider theme={theme}>
              <KeyboardTimePicker
                ampm
                className={classes.container}
                clearable
                disabled={!!disableComponents}
                value={timeValue}
                placeholder={placeholder || 'New Time'}
                onChange={handleTimeChange}
                onError={error => handleError(error)}
                format={format || 'hh:mm A'}
                mask={'__:__ _M'}
                invalidDateMessage={errorMessage || 'Invalid Time Format'}
                inputVariant='standard'
                // {...other}
              />
            </MuiThemeProvider>
          </MuiPickersUtilsProvider>
        )}
      </div>
    </div>
    // </FormControl>
  )
}
