import React from 'react'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles';
import { Popover, Grid } from '@material-ui/core'
import moment from 'moment'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import FieldType from 'Constant/TermsAndConditions'
import { CurrencyFormatter } from 'Utilities/TextFormatter'


// Styles
import classes from './ViewMore.module.scss'
import _ from "lodash";

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: "#f6f7fa",
    color: theme.palette.common.white
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(even)': {
      backgroundColor: theme.palette.action.hover
    },
    height: '45px'
  }
}))(TableRow)

export default function CustomizedDialogs (props) {
  const { updateData, setUpdateData, fromTile } = props

  const renderSwitch = (param, value) => {
    switch(param) {
      case FieldType.CURRENCY.toLowerCase():
        return value && value.length ? 'PHP ' + CurrencyFormatter(value[0]) : '-';
      case FieldType.PERCENTAGE.toLowerCase():
        return value && value.length ? value.map((val, index) => index === 0 ? val + '%' : ' - ' +val + '%') : '-';
      case FieldType.LIST.toLowerCase():
        return value && value.length ? value.map((val, index) => {
          return (
          <React.Fragment style={{marginBottom: "10px"}}>
            <li>{val} </li>
          </React.Fragment>)
        } ) : '-';
      default:
        return value && value.length ? value[0] : '-';
    }
  }

  return (
    <Popover
      open={updateData.show}
      anchorEl={updateData.show}
      onClose={() => setUpdateData(false)}
      marginThreshold={45}
      PaperProps={ { style: { width: fromTile ? '70%' : '50%', height: '500px', background: '#f4f6f8', padding: '10px' }} }
      anchorOrigin={{
        vertical: 'center',
        horizontal: fromTile ? 'center' : 'right'
      }}
      transformOrigin={{
        vertical: fromTile ? 'center' : 'top',
        horizontal: fromTile ? 'center' : 'right'
      }}
    >
      <div style={{ padding: '1rem' }}>
        <Grid container className={classes.header}>
          <Grid item xs={10}>
            <Typography variant='h6' className={classes.title} style={{ marginBottom: '10px', fontWeight: 'bold' }}>
              {updateData.title}
            </Typography>
            <Typography variant='subtitle1' className={classes.subtitle} style={{ marginBottom: '10px' }}>
              {updateData.subtitle}
            </Typography>
          </Grid>
          <Grid item xs={2}>
            <IconButton aria-label='close' style={{float: 'right', marginRight: '-10px', marginTop: '-10px'}} className={classes.button} 
              onClick={() => setUpdateData(false)}>
              <CloseIcon />
            </IconButton>
          </Grid>
        </Grid>

        <Grid container className={classes.header} style={{marginTop: '15px', marginBottom: '15px'}}>
          <Grid item xs={6}>
            <Typography variant='body2' className={classes.title} style={{ marginBottom: '5px' }}>
              SENDING MODULE
            </Typography>
            <Typography variant='body2' className={classes.subtitle} style={{ marginBottom: '5px', fontWeight: 'bold' }}>
              {updateData?.data?.sending_module}
            </Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant='body2' className={classes.title} style={{ marginBottom: '5px' }}>
              DATE RECEIVED
            </Typography>
            <Typography variant='body2' className={classes.subtitle} style={{ marginBottom: '5px', fontWeight: 'bold' }}>
            {moment(updateData?.data?.date.created).format('ll')}
            </Typography>
          </Grid>
        </Grid>

        <Grid container className={classes.header} style={{marginBottom: '15px'}}>
          <Grid item xs={6}>
            <Typography variant='body2' className={classes.title} style={{ marginBottom: '5px' }}>
              CATEGORY
            </Typography>
            <Typography variant='body2' className={classes.subtitle} style={{ marginBottom: '5px', fontWeight: 'bold' }}>
              {updateData?.data?.category}
            </Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant='body2' className={classes.title} style={{ marginBottom: '5px' }}>
              TIME
            </Typography>
            <Typography variant='body2' className={classes.subtitle} style={{ marginBottom: '5px', fontWeight: 'bold' }}>
              {moment(updateData?.data?.date.created).format('h:mm:ss A')}
            </Typography>
          </Grid>
        </Grid>

        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table" size={"small"}>
              <TableHead style={{backgroundColor: "#dddfe3"}}>
                <TableRow class="table-sub-header">
                  <TableCell width={130}> Field Name</TableCell>
                  <TableCell align={'center'} > Old Value</TableCell>
                  <TableCell align={'center'} > New Value</TableCell>
                </TableRow>
              </TableHead>
            </Table>
          </TableContainer>
        <div style={{height: '180px', overflowX: 'auto'}}>
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table" size={"small"}>
              <TableBody>
                {updateData.data && updateData.data.values && updateData.data.values.map((row, index) => (
                  <StyledTableRow>
                    <StyledTableCell component='th' width={fromTile ? 250 : 130}>
                      {row.name}
                    </StyledTableCell >
                    {row.type.toLowerCase() === FieldType.LIST.toLowerCase() ? 
                      <StyledTableCell style={{ verticalAlign: 'top' }} component='th'>
                        { renderSwitch(row.type.toLowerCase(), row.old_value) }
                      </StyledTableCell >
                    : 
                      <StyledTableCell component='th' align={'center'}>
                        { renderSwitch(row.type.toLowerCase(), row.old_value) }
                      </StyledTableCell >
                    }
                    
                    {row.type.toLowerCase() === FieldType.LIST.toLowerCase() ? 
                      <StyledTableCell style={{ verticalAlign: 'top' }} component='th'>
                        { renderSwitch(row.type.toLowerCase(), row.new_value) }
                      </StyledTableCell >
                    : 
                      <StyledTableCell component='th' align={'center'}>
                        { renderSwitch(row.type.toLowerCase(), row.new_value) }
                      </StyledTableCell >
                    }
                  </StyledTableRow >
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </div>
    </Popover>
  )
}
