import instance from '../../instance'
const axiosAS = instance('/b/as')

export default class AssignAnalystServices {

  // ( BILLING ) For Dashboard AssignAnalystTile
  static getAccountsListToAssignOnAnalyst (params) {
    // BC-017
    return axiosAS({
      url: '/setup/analyst/account/lists',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  // ( BILLING ) For Dashboard AssignAnalystTile View List
  static getAccountsViewListToAssignOnAnalyst (params) {
    return axiosAS({
      url: '/setup/analyst/account/view-lists',
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  // ( COLLECTION ) For Dashboard AccountSetup (tile)
  static getCollectionAccountsListToAssignOnAnalyst (params) {
    return axiosAS({
      url: '/setup/analyst/account/lists/collection',
      method: 'GET',
      params
    })
  }
 // ( BILLING ) AssignAnalystPage
  static analystsReportsToSupervisor (params) {
    return axiosAS({
      url: `/setup/analyst/list`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  // ( BILLING ) Assign Analyst To an Account
  static assignBillingAnalyst ({ data }) {
    return axiosAS({
      url: `/setup/analyst/assign/${data.analystId}`,
      method: 'PATCH',
      data
    })
  }
}