import instance from '../../instance'
const axios = instance('/b/cc')

export default class UpdatedAccountInformationService {
  static getUpdatedAccountInformation (params) {
    return axios({
      url: `/billing/client-contract/view/updated-information`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getUpdatedAccountInformationTile (params) {
    return axios({
      url: `/billing/client-contract/view/updated-information`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }

}